import React, { useState } from "react";
import Container from "@mui/material/Container";
import Grow from "@mui/material/Grow";
import {
  AppBar,
  Button,
  Grid,
  Paper,
  TextField,
  Typography,
} from "@mui/material";
import Posts from "../posts/Posts";
import Form from "../form/Form";
import { useDispatch, useSelector } from "react-redux";
import ChipInput from "material-ui-chip-input";
import { useHistory, useLocation } from "react-router-dom";
import { getPostsBySearch } from "../../redux/actions/postsActions";
import "./Home.css";
function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function Home() {
  console.log("Home rendered");
  const [postId, setPostId] = useState(null);
  const [searchWithTitle, setSearchWithTitle] = useState("");
  const [searchWithTags, setSearchWithTags] = useState([]);
  const myAuthData = useSelector(function (state) {
    // state = {myPostsReducer:[{title:"",message:"",creator:"",tags:["",""],selectedFile:"",_id:"",__v:""}],
    // myAuthReducer:{authData:{currentUser: {name:"",email:"",password:"",_id:"",__v:""},jwtToken:""}}}
    return state.myAuthReducer.authData;
  });
  const dispatch = useDispatch();
  const history = useHistory();
  const query = useQuery();
  const page = query.get("page") || 1;
  const searchQuery = query.get("searchQuery");
  console.log(page);
  console.log(searchQuery);

  function addChipHandler(chip) {
    setSearchWithTags([...searchWithTags, chip]);
  }
  function deleteChipHandler(chip) {
    setSearchWithTags(
      searchWithTags.filter(function (i) {
        return i != chip;
      })
    );
  }
  function keyPressHandler(event) {
    if (event.keyCode === 13) {
      searchPostHandler();
    }
  }

  // myAuthData = {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}
  function searchPostHandler() {
    if (searchWithTitle.trim() || searchWithTags.length > 0) {
      dispatch(
        getPostsBySearch({
          searchWithTitle: searchWithTitle,
          searchWithTags: searchWithTags.join(","),
        })
      );
      history.push(
        "/posts/search?title=" +
          searchWithTitle +
          "&tags=" +
          searchWithTags.join(",")
      );
    } else {
      history.push("/");
    }
  }

  return (
    <Grow in>
      <Container maxWidth="xl">
        <Grid
          container
          justify="space-between"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12} sm={6} md={9}>
            <Posts setPostId={setPostId} />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <AppBar className="Home_search" position="static" color="inherit">
              <TextField
                className="Home_textField"
                onKeyDown={keyPressHandler}
                name="search"
                variant="outlined"
                label="Search Posts"
                fullWidth
                value={searchWithTitle}
                onChange={function (event) {
                  return setSearchWithTitle(event.target.value);
                }}
              />
              <ChipInput
                style={{ margin: "10px 0" }}
                value={searchWithTags}
                fullWidth
                onAdd={function (chip) {
                  return addChipHandler(chip);
                }}
                onDelete={function (chip) {
                  return deleteChipHandler(chip);
                }}
                label="Search Tags"
                variant="outlined"
              />
              <Button
                className="Home_searchButton"
                onClick={searchPostHandler}
                className="Home_searchButton"
                variant="contained"
                color="primary"
                fullWidth
              >
                Search
              </Button>
            </AppBar>
            {myAuthData === null ? (
              <Paper elevation={6} style={{ padding: "10px" }}>
                <Typography variant="h6" align="center">
                  Please Sign In to create your own posts and like other's
                  posts.
                </Typography>
              </Paper>
            ) : (
              <Form setPostId={setPostId} postId={postId} />
            )}
          </Grid>
        </Grid>
      </Container>
    </Grow>
  );
}

export default Home;
