let initialState = { post: null, posts: [] };

console.log("Inside postsReducer.js");

function postsReducer(state = initialState, action) {
  // state = [{id:"",title:'',message:'',creator:'',tags:[""],selectedFile:""},{}]
  console.log("Inside postsReducer.js function");
  let demoState;

  switch (action.type) {
    case "CREATE_POST":
      console.log(action.payload);
      return { ...state, posts: [...state.posts, action.payload] };

    case "FETCH_POSTS":
      return { ...state, posts: action.payload };
    // action.payload = [{_id:"",title:'',message:'',creator:'',tags:["",""],selectedFile:""},{}]
    case "FETCH_BY_SEARCH":
      return { ...state, posts: action.payload };
    // action.payload =  [{_id:"",title:'',message:'',creator:'',tags:["",""],selectedFile:""},{}]
    case "FETCH_BY_POSTID":
      return { ...state, post: action.payload };
    // action.payload =  {_id:"",title:'',message:'',creator:'',tags:["",""],selectedFile:""}
    case "UPDATE_POST":
      demoState = [...state]; // can not write demoState = state, it will get the address
      for (let i = 0; i < demoState.length; i++) {
        if (demoState[i]._id === action.payload._id) {
          demoState[i] = { ...demoState[i], ...action.payload };
        }
      }

      return demoState;

    case "DELETE_POST":
      demoState = []; // can not write demoState = state, it will get the address
      for (let i = 0; i < state.length; i++) {
        if (state[i]._id !== action.payload) {
          demoState.push({ ...state[i] });
        }
      }
      return demoState;
    case "LIKE_POST":
      demoState = [...state]; // can not write demoState = state, it will get the address
      for (let i = 0; i < demoState.length; i++) {
        if (demoState[i]._id === action.payload._id) {
          demoState[i] = { ...demoState[i], ...action.payload };
        }
      }

      return demoState;
    default:
      console.log("Default State " + state);
      return state; // []
  }
}
export default postsReducer;
