import { Card, CardMedia, Typography, Button, ButtonBase } from "@mui/material";
import React from "react";
import moment from "moment";
import "./SinglePost.css";
import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import DeleteIcon from "@mui/icons-material/Delete";
import { useDispatch, useSelector } from "react-redux";
import { deletePost, likePost } from "../../../redux/actions/postsActions";
import ThumbUpOutlinedIcon from "@mui/icons-material/ThumbUpOutlined";
import EditIcon from "@mui/icons-material/Edit";
import { useHistory } from "react-router-dom";

function SinglePost(props) {
  // props = {post:{title:"",message:"",postCreator:"",tags:[""],selectedFile:"",likes:[""],createdAt:"",_id:"",__v:""}}
  console.log("SinglePost rendered");
  const myAuthData = useSelector(function (state) {
    // state = {myPostsReducer:[{title:"",message:"",postCreator:"",tags:[""],selectedFile:"",likes:[""],createdAt:"",_id:"",__v:""},{}],
    // myAuthReducer:{authData:{currentUser: {name:"",email:"",password:"",_id:"",__v:""},jwtToken:""}}}
    return state.myAuthReducer.authData;
  });
  console.log(myAuthData);

  let myLikedPost = -1;
  if (myAuthData != null) {
    myLikedPost = props.post.likes.findIndex(function (userId) {
      return userId === myAuthData.currentUser._id;
    }); //it can be any int value(userId found) or, -1(userId not found)
  }

  const dispatch = useDispatch();
  const history = useHistory();

  function onClickDataLoad() {
    console.log("onClickDataLoad");
    props.setPostId(props.post._id);
    console.log("onClickDataLoading");
  }
  function deleteHandler() {
    dispatch(deletePost(props.post._id));
  }
  function onClickLikePost() {
    dispatch(likePost(props.post._id));
  }
  function openPostDetailsHandler() {
    history.push("/posts/" + props.post._id);
  }

  return (
    <Card
      sx={{ maxWidth: 345 }}
      style={{
        border: "1px solid lightgray",
      }}
    >
      <ButtonBase
        component="span"
        name="test"
        style={{ display: "block", textAlign: "initial" }}
        onClick={openPostDetailsHandler}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            margin: "6px",
          }}
        >
          <div>
            <Typography variant="h6" style={{ fontWeight: 350 }}>
              Creator
            </Typography>
            <Typography variant="body2">
              {moment(props.post.createdAt).fromNow()}
            </Typography>
          </div>
          <Button
            style={{
              backgroundColor: "#9c27b0",
              color: "white",
              borderRadius: "50%",
            }}
            onClick={onClickDataLoad}
          >
            <EditIcon />
          </Button>
        </div>

        <CardMedia
          style={{ marginTop: "15px", objectFit: "cover", marginBottom: "6px" }}
          component="img"
          height="194"
          width="100%"
          image={props.post.selectedFile}
          alt="Paella dish"
        />
        <div style={{ marginBottom: "10px", marginLeft: "6px" }}>
          <Typography className="SinglePost_details" variant="body2">
            {props.post.tags.map(function (i) {
              return "#" + i + " ";
            })}
          </Typography>
          <Typography
            style={{
              fontWeight: 550,
              marginTop: "10px",
              marginBottom: "10px",
            }}
            align="center"
            variant="h5"
          >
            {props.post.title}
          </Typography>
          <Typography
            variant="body2"
            color="textSecondary"
            style={{ marginLeft: "6px" }}
          >
            {/*This impressive paella is a perfect party dish and a fun meal to cook
          together with your guests. Add 1 cup of frozen peas along with the
          mussels, if you like.*/}
            {props.post.message}
          </Typography>
        </div>
      </ButtonBase>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          marginLeft: "6px",
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
          }}
        >
          {myLikedPost === -1 ? (
            <ThumbUpOutlinedIcon
              style={{ margin: "4px" }}
              fontSize="small"
              onClick={onClickLikePost}
              cursor="pointer"
            />
          ) : (
            <ThumbUpIcon
              style={{ margin: "4px" }}
              fontSize="small"
              color="primary"
              onClick={onClickLikePost}
              cursor="pointer"
            />
          )}

          <Typography variant="body2" color="text.secondary">
            {"Like " +
              (props.post.likes.length > 0 ? props.post.likes.length : "")}
          </Typography>
        </div>
        {myAuthData != null &&
        myAuthData.currentUser._id === props.post.postCreator ? (
          <Button size="small" color="primary" onClick={deleteHandler}>
            <DeleteIcon fontSize="small" />
            Delete
          </Button>
        ) : (
          ""
        )}
      </div>
    </Card>
  );
}

export default SinglePost;
