import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import { getPostById } from "../../../redux/actions/postsActions";
import {
  Paper,
  Typography,
  CircularProgress,
  Divider,
} from "@material-ui/core/";
import moment from "moment";
import "./PostDetails.css";

function PostDetails() {
  const post = useSelector(function (state) {
    // state = {myPostsReducer:{post:{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",likes:[""],createdAt:"",_id:"",__v:""},posts:[{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",likes:[""],createdAt:"",_id:"",__v:""},{}]},
    // myAuthReducer:{authData:{currentUser: {name:"",email:"",password:"",_id:"",__v:""},jwtToken:""}}}
    return state.myPostsReducer.post;
  });
  const dispatch = useDispatch();
  const params = useParams(); //params={postId:""}

  console.log(params);
  useEffect(function () {
    dispatch(getPostById(params.postId));
  }, []);
  return (
    <Paper style={{ padding: "20px", borderRadius: "15px" }} elevation={6}>
      {
        post === null ? (
          ""
        ) : (
          <div className="PostDetails_card">
            <div className="PostDetails_section">
              <Typography variant="h3" component="h2">
                {post.title}
              </Typography>
              <Typography
                gutterBottom
                variant="h6"
                color="textSecondary"
                component="h2"
              >
                {post.tags.map((tag) => `#${tag} `)}
              </Typography>
              <Typography gutterBottom variant="body1" component="p">
                {post.message}
              </Typography>
              <Typography variant="h6">Created by: {post.name}</Typography>
              <Typography variant="body1">
                {moment(post.createdAt).fromNow()}
              </Typography>
              <Divider style={{ margin: "20px 0" }} />
              <Typography variant="body1">
                <strong>Realtime Chat - coming soon!</strong>
              </Typography>
              <Divider style={{ margin: "20px 0" }} />
              <Typography variant="body1">
                <strong>Comments - coming soon!</strong>
              </Typography>
              <Divider style={{ margin: "20px 0" }} />
            </div>
            <div className="PostDetails_imageSection">
              <img
                className="PostDetails_media"
                src={
                  post.selectedFile ||
                  "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
                }
                alt={post.title}
              />
            </div>
          </div>
        )
        /*!!recommendedPosts.length && (
        <div className="PostDetails_section">
          <Typography gutterBottom variant="h5">
            You might also like:
          </Typography>
          <Divider />
          <div className="PostDetails_recommendedPosts">
            {recommendedPosts.map(
              ({ title, name, message, likes, selectedFile, _id }) => (
                <div
                  style={{ margin: "20px", cursor: "pointer" }}
                  onClick={() => openPost(_id)}
                  key={_id}
                >
                  <Typography gutterBottom variant="h6">
                    {title}
                  </Typography>
                  <Typography gutterBottom variant="subtitle2">
                    {name}
                  </Typography>
                  <Typography gutterBottom variant="subtitle2">
                    {message}
                  </Typography>
                  <Typography gutterBottom variant="subtitle1">
                    Likes: {likes.length}
                  </Typography>
                  <img src={selectedFile} width="200px" />
                </div>
              )
              )}
          </div>
        </div>
              )*/
      }
    </Paper>
  );
}

export default PostDetails;
