const initialState = { authData: null };

console.log("Inside authReducer.js");

function authReducer(state = initialState, action) {
  console.log("Inside authReducer.js function");

  switch (action.type) {
    case "CREATE_USER":
      // action.payload = {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}
      localStorage.setItem("auth", JSON.stringify(action?.payload));
      return { ...state, authData: action?.payload };

    case "SIGN_IN_USER":
      // action.payload = {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}
      localStorage.setItem("auth", JSON.stringify(action?.payload));
      return { ...state, authData: action?.payload };

    case "LOGOUT":
      localStorage.clear();
      return { ...state, authData: null };

    case "LOAD_USER":
      // in local storage -> {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}
      return { ...state, authData: JSON.parse(localStorage.getItem("auth")) };

    default:
      return state;
  }
}

export default authReducer;
