import React, { useEffect } from "react";
import SocialMedia from "../../images/SocialMedia.png";
import AppBar from "@mui/material/AppBar";
import Typography from "@mui/material/Typography";
import "./NavBar.css";
import { Link } from "react-router-dom";
import { Avatar, Button, Container, Toolbar } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";

function NavBar() {
  console.log("NavBar rendered");
  const myAuthData = useSelector(function (state) {
    // state = {myPostsReducer:[{title:"",message:"",creator:"",tags:["",""],selectedFile:"",_id:"",__v:""}],
    // myAuthReducer:{authData:{currentUser: {name:"",email:"",password:"",_id:"",__v:""},jwtToken:""}}}
    return state.myAuthReducer.authData;
  });

  // myAuthData = {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}
  const dispatch = useDispatch();
  const history = useHistory();

  console.log(myAuthData);

  function logoutHandler() {
    dispatch({ type: "LOGOUT" });
    history.push("/");
  }
  useEffect(function () {
    console.log("NavBar useEffect");
    dispatch({ type: "LOAD_USER" });
  }, []);

  return (
    <AppBar className="NavBar_appbar" position="static" color="inherit">
      <Link to="/" style={{ textDecoration: "none" }}>
        <div className="NavBar_brandContainer">
          <Typography variant="h2" align="center">
            Social Media
          </Typography>
          <img className="NavBar_image" src={SocialMedia} alt="" height="70" />
        </div>
      </Link>

      <Toolbar className="NavBar_toolbar">
        {myAuthData == null ? (
          <Link to="/auth" style={{ textDecoration: "none" }}>
            <Button variant="contained" color="secondary">
              Sign In
            </Button>
          </Link>
        ) : (
          <div className="NavBar_profile">
            <Avatar
              className="NavBar_purple"
              alt={myAuthData?.currentUser.name}
              //src={myAuthData?.currentUser.imageUrl}
            >
              {myAuthData?.currentUser.name.charAt(0)}
            </Avatar>
            <Typography className="NavBar_userName" variant="h6">
              {myAuthData?.currentUser.name}
            </Typography>
            <Button
              variant="contained"
              color="secondary"
              onClick={logoutHandler}
            >
              Logout
            </Button>
          </div>
        )}
      </Toolbar>
    </AppBar>
  );
}

export default NavBar;
