import React, { useEffect, useState } from "react";
import { TextField, Typography, Paper, Button } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { createPost, updatePost } from "../../redux/actions/postsActions";
import FileBase from "react-file-base64";
import "./Form.css";

function Form(props) {
  console.log("Form rendered");

  const dispatch = useDispatch();
  const allPosts = useSelector(function (state) {
    // state = {myPostsReducer:[{title:"",message:"",creator:"",tags:["",""],selectedFile:"",_id:"",__v:""}],
    // myAuthReducer:{authData:{currentUser: {name:"",email:"",password:"",_id:"",__v:""},jwtToken:""}}}
    return state.myPostsReducer;
  });

  const [postData, setPostData] = useState({
    title: "",
    message: "",
    tags: "",
    selectedFile: "",
  });

  console.log(postData);

  function clearHandler() {
    props.setPostId(null);
    setPostData({
      title: "",
      message: "",
      tags: "",
      selectedFile: "",
    });
  }
  function onSubmitHandler(event) {
    event.preventDefault();

    if (props.postId === null) {
      dispatch(createPost(postData)); //it gets a function not an object
      // postData = {title:"",message:"",tags:[""],selectedFile:""}

      console.log("Submitting");
      clearHandler();
    } else {
      dispatch(updatePost(postData)); //it gets a function not an object
      // postData = {title:"",message:"",creator:"",tags:[""],
      // selectedFile:"",likeCount:"",createdAt:"",_id:"",__v:""}

      console.log("Editing");
      clearHandler();
      props.setPostId(null);
    }
  }

  useEffect(
    function () {
      if (props.postId != null) {
        const post = allPosts.find(function (i) {
          return i._id === props.postId;
        });
        setPostData(post);
      }
    },
    [props.postId]
  );

  return (
    <Paper className="Form_paper">
      <form
        autoComplete="off"
        onSubmit={onSubmitHandler}
        className="form"
        noValidate
      >
        <Typography
          style={{
            fontWeight: 510,
          }}
          align="center"
          variant="h5"
        >
          {props.postId === null ? "Create a Post" : "Edit Post"}
        </Typography>

        <TextField
          name="title"
          variant="outlined"
          label="Title"
          fullWidth
          value={postData.title}
          onChange={function (event) {
            setPostData({ ...postData, title: event.target.value });
          }}
          className="Form_textField"
        />
        <TextField
          name="message"
          variant="outlined"
          label="Message"
          fullWidth
          value={postData.message}
          onChange={function (event) {
            setPostData({ ...postData, message: event.target.value });
          }}
          className="Form_textField"
        />
        <TextField
          name="tags"
          variant="outlined"
          label="Tags (Coma Separated)"
          value={postData.tags}
          fullWidth
          onChange={function (event) {
            setPostData({ ...postData, tags: event.target.value.split(",") });
          }}
          className="Form_textField"
        />
        <div className="Form_fileInput">
          <FileBase
            type="file"
            multiple={false}
            value={postData.selectedFile}
            onDone={function ({ base64 }) {
              return setPostData({ ...postData, selectedFile: base64 });
            }}
          />
        </div>
        <Button
          className="buttonSubmit"
          variant="contained"
          color="primary"
          size="large"
          type="submit"
          fullWidth
        >
          {props.postId === null ? "Submit" : "Edit"}
        </Button>
        <Button
          variant="contained"
          color="secondary"
          size="large"
          fullWidth
          onClick={clearHandler}
          className="Form_textField"
        >
          Clear
        </Button>
      </form>
    </Paper>
  );
}

export default Form;
