import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import SinglePost from "./singlePost/SinglePost";
import { useSelector } from "react-redux";
import { getPosts } from "../../redux/actions/postsActions";
import { CircularProgress, Grid } from "@mui/material";

function Posts(props) {
  console.log("Posts rendered");

  const allPosts = useSelector(function (state) {
    // state = {myPostsReducer:{post:{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",likes:[""],createdAt:"",_id:"",__v:""},posts:[{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",likes:[""],createdAt:"",_id:"",__v:""},{}]},
    // myAuthReducer:{authData:{currentUser: {name:"",email:"",password:"",_id:"",__v:""},jwtToken:""}}}
    return state.myPostsReducer.posts;
  });

  console.log(allPosts);
  // allposts = [{title:"",message:"",creator:"",tags:["",""],selectedFile:"",_id:"",__v:""}]

  const dispatch = useDispatch();

  useEffect(function () {
    console.log("Insite Posts useEffect");
    dispatch(getPosts());
  }, []);
  return (
    <div>
      {allPosts.length === 0 ? (
        <CircularProgress />
      ) : (
        <Grid className="Posts_grid" container alignItems="stretch" spacing={3}>
          {allPosts.map(function (i, index) {
            return (
              <Grid item xs={12} sm={6} md={6} key={index}>
                <SinglePost post={i} setPostId={props.setPostId} />
              </Grid>
            );
          })}
        </Grid>
      )}
    </div>
  );
}

export default Posts;
