import axios from "axios";

// in local storage -> {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken:""}
const axiosInstance = axios.create({
  baseURL: "http://localhost:4040", //BASE API URL
  //(only to debug for development time) we have to change this for deployment
});

axiosInstance.interceptors.request.use(function (req) {
  if (localStorage.getItem("auth")) {
    req.headers.Authorization =
      "Bearer " + JSON.parse(localStorage.getItem("auth")).jwtToken;
  }
  return req;
});

function createPost(postData) {
  // postData = {title:"",message:"",tags:[""],selectedFile:""}
  return async function (dispatch) {
    //not right
    const response = await axiosInstance.post("/posts", postData);
    console.log(response);

    const responseData = response.data;

    console.log(responseData);
    // responseData = {title:"",message:"",postCeator:"",tags:[""],
    // selectedFile:"",like:[""],createdAt:"",_id:"",__v:""}

    dispatch({
      type: "CREATE_POST",
      payload: { ...responseData },
    });
  };
}
function getPosts() {
  return async function (dispatch) {
    const response = await axiosInstance.get("/posts");
    const responseData = response.data;

    console.log(responseData);
    //[{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",like:[""],createdAt:"",_id:"",__v:"",{}]

    dispatch({ type: "FETCH_POSTS", payload: responseData });
  };
}
function getPostsBySearch(searchQuery) {
  // searchQuery={searchWithTitle:"",searchWithTags:",",}
  console.log(searchQuery);
  return async function (dispatch) {
    const response = await axiosInstance.get(
      "/posts/search?title=" +
        searchQuery.searchWithTitle +
        "&tags=" +
        searchQuery.searchWithTags
    );
    const responseData = response.data.allData;

    console.log(responseData);
    //[{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",like:[""],createdAt:"",_id:"",__v:"",{}]

    dispatch({ type: "FETCH_BY_SEARCH", payload: responseData });
  };
}

function getPostById(postId) {
  return async function (dispatch) {
    const response = await axiosInstance.get("/posts/" + postId);
    const responseData = response.data;

    console.log(responseData);
    //{title:"",message:"",postCeator:"",tags:[""],selectedFile:"",like:[""],createdAt:"",_id:"",__v:""}

    dispatch({ type: "FETCH_BY_POSTID", payload: responseData });
  };
}

function updatePost(postData) {
  // postData = {title:"",message:"",postCeator:"",tags:[""],
  // selectedFile:"",like:[""],createdAt:"",_id:"",__v:""}

  const postId = postData._id;
  return async function (dispatch) {
    console.log(postId);

    const response = await axiosInstance.put("/posts?id=" + postId, postData);
    const responseData = response.data;

    console.log(responseData);
    // responseData = {title:"",message:"",postCeator:"",tags:[""],
    // selectedFile:"",like:[""],createdAt:"",_id:"",__v:""}

    dispatch({ type: "UPDATE_POST", payload: responseData });
  };
}

function deletePost(postId) {
  return async function (dispatch) {
    console.log(postId);

    const url = `/posts?id=${postId}`;
    const response = await axiosInstance.delete(url);
    const responseData = response.data;

    console.log(responseData); // responseData = {message:"Deleted Successfully"}

    dispatch({ type: "DELETE_POST", payload: postId });
  };
}

function likePost(postId) {
  return async function (dispatch) {
    console.log(postId);
    const url = `http://localhost:4040/posts/likePost?id=${postId}`;
    const response = await axiosInstance.put(url);
    const responseData = response.data;

    console.log(responseData); //{title:"",message:"",creator:"",tags:[""],selectedFile:"",_id:""}

    dispatch({ type: "LIKE_POST", payload: responseData });
  };
}

export {
  createPost,
  getPosts,
  updatePost,
  deletePost,
  likePost,
  getPostsBySearch,
  getPostById,
};
