import {
  Avatar,
  Button,
  Container,
  Paper,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import "./Auth.css";
import { useDispatch } from "react-redux";
import { signUpUser, signInUser } from "../../redux/actions/authActions";
import { useHistory } from "react-router";

function Auth() {
  console.log("Auth rendered");
  const history = useHistory();
  const [isSignUp, setIsSignUp] = useState(false);
  const [userData, setUserData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    confirmPassword: "",
  });

  const dispatch = useDispatch();
  console.log(userData);

  function onSubmitHandler(event) {
    event.preventDefault();
    if (isSignUp) {
      dispatch(signUpUser(userData)); //it gets a function not an object
      // userData = {firstName:"",lastName:"",email:"",password:"",confirmPassword:""}
      setUserData({
        // it is not executed
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: "",
      });
      history.push("/");
    } else {
      dispatch(signInUser(userData)); //it gets a function not an object
      // userData = {firstName:"",lastName:"",email:"",password:"",confirmPassword:""}
      setUserData({
        // it is not executed
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: "",
      });
      history.push("/");
    }
  }

  function switchMode() {
    setIsSignUp(function (prevState) {
      return !prevState;
    });
  }

  return (
    <Container component="main" maxWidth="xs">
      <Paper className="Auth_paper" elevation={3}>
        <Avatar className="Auth_avatar">
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          {isSignUp ? "Sign Up" : "Sign In"}
        </Typography>
        <form
          autoComplete="off"
          className="Auth_form"
          onSubmit={onSubmitHandler}
          noValidate
        >
          {isSignUp ? (
            <div>
              <TextField
                name="First Name"
                label="First Name"
                value={userData.firstName}
                onChange={(event) => {
                  setUserData({
                    ...userData,
                    firstName: event.target.value,
                  });
                }}
                type="text"
                variant="outlined"
                required
                fullWidth
                className="Auth_textField"
              />
              <TextField
                name="Last Name"
                label="Last Name"
                value={userData.lastName}
                onChange={(event) => {
                  setUserData({ ...userData, lastName: event.target.value });
                }}
                type="text"
                variant="outlined"
                required
                fullWidth
                className="Auth_textField"
              />
            </div>
          ) : (
            ""
          )}
          <TextField
            name="Email Address"
            label="Email Address"
            value={userData.email}
            onChange={(event) => {
              setUserData({ ...userData, email: event.target.value });
            }}
            type="text"
            variant="outlined"
            required
            fullWidth
            className="Auth_textField"
          />
          <TextField
            name="Password"
            label="Password"
            value={userData.password}
            type="password"
            variant="outlined"
            required
            fullWidth
            className="Auth_textField"
            onChange={(event) => {
              setUserData({ ...userData, password: event.target.value });
            }}
          />
          {isSignUp ? (
            <TextField
              name="Repeat Password"
              value={userData.confirmPassword}
              label="Repeat Password"
              onChange={(event) => {
                setUserData({
                  ...userData,
                  confirmPassword: event.target.value,
                });
              }}
              type="password"
              variant="outlined"
              required
              fullWidth
              className="Auth_textField"
            />
          ) : (
            ""
          )}
          <Button
            type="submit"
            fullWidth
            className="Auth_button"
            variant="contained"
            color="primary"
          >
            {isSignUp ? "SIGN UP" : "SIGN IN"}
          </Button>
          <Button onClick={switchMode} fullWidth className="Auth_button">
            {isSignUp
              ? "Already have an account? Sign in"
              : "Don't have an account? Sign Up"}
          </Button>
        </form>
      </Paper>
    </Container>
  );
}

export default Auth;
