import axios from "axios";

function signUpUser(userData) {
  // userData = {firstName:"",lastName:"",email:"",password:"",confirmPassword:""}
  return async function (dispatch) {
    const response = await axios.post(
      "http://localhost:4040/auth/signup",
      userData
    );
    console.log(response);
    const responseData = response.data;
    // response.data = {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}

    console.log(responseData);
    // {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}
    dispatch({
      type: "CREATE_USER",
      payload: { ...responseData },
    });
  };
}
function signInUser(userData) {
  // userData = {firstName:"",lastName:"",email:"",password:"",confirmPassword:""}
  return async function (dispatch) {
    const response = await axios.post(
      "http://localhost:4040/auth/signin",
      userData
    );
    const responseData = response.data;
    // response.data = {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}

    console.log(responseData);
    // {currentUser: {name:"",email:"",password:"",_id:"",__v:""}, jwtToken: ""}

    dispatch({
      type: "SIGN_IN_USER",
      payload: { ...responseData },
    });
  };
}

export { signUpUser, signInUser };
